import React from 'react';
import style from './Nav.module.css'
import { Link, animateScroll as scroll } from "react-scroll";

function Nav() {
    return (
        <div className={style.nav}>
            <Link
                className={style.a}
                activeClass={style.active}
                to="home"
                spy={true}
                smooth={true}
                offset={1}
                duration={500}
            >Home</Link>
            {/*<a className={style.a} href="#home">Home</a>*/}
            <Link className={style.a}
                activeClass={style.active}
                to="skills"
                spy={true}
                smooth={true}
                offset={1}
                duration={500}
            >Skills</Link>
            {/*<a className={style.a} href="#skills">Skills</a>*/}
            <Link className={style.a}
                activeClass={style.active}
                to="projects"
                spy={true}
                smooth={true}
                offset={1}
                duration={500}
            >Projects</Link>
            {/*<a className={style.a} href="#projects">Projects</a>*/}
            <Link className={style.a}
                activeClass={style.active}
                to="footer"
                spy={true}
                smooth={true}
                  // докрутка 1
                offset={1}
                duration={500}
            >Contact</Link>
            {/*<a className={style.a} href="#footer">Contact</a>*/}

        </div>
    );
}

export default Nav;
