import React from 'react';
import style from './MyWorks.module.css'
import styleContainer from "../common/styles/Conteiner.module.css";
import Work from "./work/Work";
import Title from "../components/title/Title";
import socialImage from "./work/img/block2.jpg"
import Flip from 'react-reveal/Flip';

function MyWorks() {
    const social = {
        backgroundImage: `url(${socialImage})`
    }

    const todolist = {
        backgroundImage: `url(${socialImage})`
    }
    return (
        <div id="projects" className={style.myWorksBlock}>

            <div className={`${styleContainer.container} ${style.myWorksContainer}`}>
                <Flip>
                    <Title text={'Projects'}/>
                    <div className={style.myWork}>
                            <Work style={social} href={'https://alehstatsevich.github.io/-social-network/'} view={'View'}
                                  title={'Social Network'}
                                  description={'technologies were used such as:React,Redux,JS,TS,react-router-dom,axios,yarn.(Projects in process)'}/>
                            <Work style={todolist} href={'https://alehstatsevich.github.io/todolist/'} view={'View'}
                                  title={'Todolist'}
                                  description={'technologies were used such as:React,Redux,JS,TS,react-router-dom,axios,material-ui,yarn.(Projects in process)'}/>
                    </div>
                </Flip>
            </div>
        </div>
    );
}

export default MyWorks;
