import React from 'react';
import style from "./Footer.module.css"
import styleContainer from "../common/styles/Conteiner.module.css";
import Fade from 'react-reveal/Fade';


function Footer() {
    return (
        <div id="footer" className={style.main}>
            <div className={`${styleContainer.container} ${style.mainContainer}`}>
                <Fade bottom big>
                    <h2 className={style.shel}>Aleh Statsevich</h2>
                    <div className={style.icons}>
                        <ul>
                            {/*<li>*/}
                            <a className={style.a} href='https://github.com/alehStatsevich'>GitHub</a>
                            {/*</li>*/}
                            {/*<li>*/}
                            <a className={style.a} href='https://alehstatsevich@gmail.com'>Mail</a>
                            {/*</li>*/}
                            {/*<li>*/}
                            <a className={style.a}
                               href='https://www.linkedin.com/in/oleg-stacevich-a71061209/'>Linkedin</a>
                            {/*</li>*/}
                            {/*<li>*/}
                            <a className={style.a} href='https://t.me/userAleh'>Telegram</a>
                            {/*</li>*/}
                        </ul>
                    </div>
                </Fade>
                <p>© 2021 beingeorge, All Rights Reserved.</p>
            </div>
        </div>
    );
}

export default Footer;
