import React from 'react';
import style from './Main.module.css';
import image from './../../src/common/img/main.jpg'
import styleContainer from './../common/styles/Conteiner.module.css'
import TypedReactDemo from './text'
import Particles from "react-particles-js";
import Zoom from 'react-reveal/Zoom';
import Tilt from 'react-tilt'

function Main() {
    return (
        <div id="home" className={style.mainBlock}>
            <Particles
                className={style.particles}
                params={{
                    "particles": {
                        "number": {
                            "value": 50
                        },
                        "size": {
                            "value": 3
                        }
                    },
                    "interactivity": {
                        "events": {
                            "onhover": {
                                "enable": true,
                                "mode": "repulse"
                            }
                        }
                    }
                }}/>
            <Zoom>
                <div className={styleContainer.container}>
                    <Particles
                        className={style.particles}
                        params={{
                            "particles": {
                                "number": {
                                    "value": 50
                                },
                                "size": {
                                    "value": 3
                                }
                            },
                            "interactivity": {
                                "events": {
                                    "onhover": {
                                        "enable": true,
                                        "mode": "repulse"
                                    }
                                }
                            }
                        }}/>
                    <div className={style.text}>
                        <span className={style.intro}>Hi There</span>
                        <h1>
                            I am
                            <span>
                            Aleh Statsevich
                        </span>
                        </h1>
                        <p>
                            A <span>
                        <TypedReactDemo
                            strings={[
                                'Frontend Developer.',
                                'React Developer.',
                                'Frontend Developer.',
                                'React Developer.',
                            ]}
                        />
                            </span>
                        </p>
                    </div>
                    <div className={style.heroBanner}>
                        <div className={style.heroRight}>
                            <Tilt className="Tilt" options={{max: 25}}>
                                <img className={style.photo} src={image} alt="my-img"/>
                                <span></span>
                            </Tilt>
                        </div>
                    </div>
                </div>
            </Zoom>
        </div>
    );
}

export default Main;
