import React from 'react';
import style from './Contact.module.css'
import styleContainer from "../common/styles/Conteiner.module.css";
import Title from "../components/title/Title";
import Fade from 'react-reveal/Fade';
import { useFormik } from 'formik';

function Contact() {
    const formik = useFormik({
        initialValues: {
            email: '',
            name: '',
            text: '',
        },
        onSubmit: values => {
            alert(JSON.stringify(values, null, 2));
        },
    });
    return (
        <div className={style.contactBlock}>
            <div className={`${styleContainer.container} ${style.contactContainer}`}>
                <Fade bottom big>
                <Title text={'Contact'}/>
                <div className={style.formBlock}>
                    <form className={style.block} onSubmit={formik.handleSubmit}>
                        <div className={style.ajaxBlock}>
                            <div className={style.formGroup}>
                            <input id="name"
                                   type="text"
                                   name="name"
                                   className={style.emailName}
                                   placeholder="Name"
                                   onChange={formik.handleChange}
                                   value={formik.values.name}
                            />
                            </div>
                                <div className={style.formGroup}>
                            <input id="email"
                                   type="email"
                                   name="email"
                                   className={style.emailName}
                                   placeholder="Email"
                                   onChange={formik.handleChange}
                                   value={formik.values.email}
                            />
                                </div>
                            <div className={style.formGroup}>
                            <textarea id="text"
                                      type="text"
                                      name="text"
                                      className={style.message}
                                      placeholder="Message"
                                      onChange={formik.handleChange}
                                      value={formik.values.text}
                            ></textarea>
                            </div>
                            <button type="submit" className={style.send}>SEND MESSAGE</button>
                        </div>
                    </form>
                </div>
                    </Fade>
            </div>
        </div>
    );
}

export default Contact;
